@import "~normalize.css";
@import "~@blueprintjs/core/lib/css/blueprint.css";
@import "~@blueprintjs/core/lib/scss/variables";
@import "nav";

html, body {
  display: flex;
  width: 100%;
  height: 100%;
}

body {
  padding-top: $pt-navbar-height;
  overflow: hidden;
}

.sterling {
  display: flex;
  width: 100%;
  height: 100%;
}

.stage {
  display: flex;
  flex: 1 0 auto;
}

.placeholder {
  flex: 1 0 auto;
  justify-content: center;
  align-items: center;
}

.divider {
  flex: 0 0 auto;
  min-width: 7px;
  width: 7px;
  background-color: #394b59;
}

.graph-view {
  flex: 1 1 0;
  display: flex;
  flex-direction: column;
  width: 0;
  overflow: auto;
  .temp-tables {
    flex: 1 0 auto;
    display: flex;
    overflow: auto;
    padding: 10px
  }
  .table-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px;
  }
  table {
    margin: 10px;
    border: 1px solid $gray5;
    td, th {
      text-align: center;
    }
  }
}